html {
  font-size: 16px;
  font-family: "droidSans", tahoma, sans-serif;
  font-weight: 400;
  color: #000000;
  box-sizing: border-box;
  height: 100vh; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a[href] {
  text-decoration: none;
  transition: 0.3s; }
  a[href]:hover {
    color: #474747; }
  a[href]:active {
    opacity: 0.5; }

img {
  max-width: 100%;
  min-height: 100%; }

.container {
  margin: 0 auto;
  max-width: 940px; }

.header {
  background: rgba(30, 9, 6, 0.15);
  height: 110px; }
  .header ul {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0; }

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%; }

.header__nav {
  margin-right: 44px; }
  .header__nav li:not(:last-of-type) {
    margin-right: 40px; }
  .header__nav a {
    font-size: 14px;
    color: #000000;
    letter-spacing: 0.08em; }
    .header__nav a:hover {
      border-bottom: 1px solid #000000; }

.header__list-icon li:not(:last-of-type) {
  margin-right: 34px; }

.header__list-icon li:first-of-type svg {
  fill: none; }

.header__list-icon li:last-of-type {
  margin-right: 13px; }
  .header__list-icon li:last-of-type a {
    display: block;
    position: relative;
    padding-right: 7px; }
    .header__list-icon li:last-of-type a svg {
      transition: 0.3s; }
    .header__list-icon li:last-of-type a:active {
      opacity: 1; }
      .header__list-icon li:last-of-type a:active svg {
        opacity: 0.5; }

.header__list-icon span {
  position: absolute;
  top: 0;
  right: 0;
  color: #f20505;
  font-size: 10px; }

.footer {
  background: #3f3f3f;
  min-height: 273px;
  padding-top: 60px;
  color: #ffffff; }
  .footer ul {
    list-style: none;
    padding: 0;
    margin: 0; }

.footer__wrapper {
  display: flex; }

.footer__links {
  flex: 2; }

.footer__logo {
  margin-bottom: 55px; }

.footer__socials ul {
  display: flex; }

.footer__socials li:not(:last-of-type) {
  margin-right: 20px; }

.footer__info {
  flex: 1; }
  .footer__info h2 {
    margin: 15px 0;
    font-weight: 400;
    font-size: 14px;
    letter-spacing: 0.1em; }
  .footer__info p {
    margin: 0 0 5px 0;
    font-size: 10px; }
  .footer__info a {
    color: #ffffff;
    font-size: 10px;
    letter-spacing: 0.1em; }
    .footer__info a[href^="tel"] {
      display: block;
      margin-bottom: 10px; }
  .footer__info li {
    line-height: 50%; }
  .footer__info li:not(:last-of-type) {
    margin-bottom: 5px; }
  .footer__info address {
    font-style: normal;
    font-size: 10px; }
